@mixin mobile{
    @media screen and (max-width:480px){
        @content;
    }
}

@mixin tablet{
    @media screen and (max-width:768px){
        @content;
    }
}

@mixin laptop{
    @media screen and (max-width:1200px){
        @content;
    }
}

.formContainer {
    background-color: #a7bcff;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;

    .formWrapper {
        background-color: white;
        padding: 20px 60px;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        gap: 10px;
        align-items: center;

        .logo {
            color: #5d5b8d;
            font-weight: bold;
            font-size: 25px;
        }

        .title {
            font-size: 15px;
        }

        form {
            display: flex;
            flex-direction: column;
            gap: 15px;

            input {
                padding: 15px;
                border: none;
                border-bottom: 1px solid #a7bcff;
                width: 250px;
                outline: none;

                &::placeholder {
                    color: rgb(175, 175, 175);
                }
            }

            button {
                background-color: #7b96ec;
                color: white;
                padding: 10px;
                font-weight: bold;
                border: none;
                cursor: pointer;
            }

            span{
                text-align: center;
            }

            label {
                display: flex;
                align-items: center;
                cursor: pointer;
                gap: 10px;

                img {
                    width: 35px;
                }

                span {
                    font-size: 15px;
                    color: #8da4f1;
                }
            }
        }

        button {
            background-color: #7b96ec;
            color: white;
            padding: 10px;
            font-weight: bold;
            border: none;
            cursor: pointer;
            width: 100%;
        }

        p {
            color: #5d5b8d;
            font-size: 15px;
            margin-top: 10px;
        }
    }
}

.home {
    background-color: #a7bcff;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;

    .container {
        border: 1px solid white;
        border-radius: 20px;
        width: 70%;
        height: 80%;
        display: flex;
        overflow: hidden;

        @include tablet{
            width: 90%;
        }

        .sidebar{
            flex: 1;
            border-right: 1px solid #3e3c61;
            background-color: #3e3c61;
            position: relative;

            .navbar{
                display: flex;
                align-items: center;
                background-color: #2f2d52;
                height: 50px;
                padding: 10px;
                justify-content: space-between;
                color: #ddddf7; 

                .logo{
                    font-weight: bold;
                    font-size: 22px;

                    @include tablet{
                        display: none;
                    }
                }

                .user{
                    display: flex;
                    gap: 10px;

                    img{
                        background-color: #ddddf7;
                        height: 25px;
                        width: 25px;
                        border-radius: 50%;
                        object-fit: cover;
                    }

                    button{
                        background-color: #5d5b8d;
                        color: #ddddf7;
                        font-size: 10px;
                        cursor: pointer;
                        border: none;

                        @include tablet{
                            position: absolute;
                            bottom: 0px;
                            width: 100%;
                            height: 40px;
                            font-weight: bold;
                            font-size: 12px;
                            left: 0px;
                        }
                    }
                }
            }

            .search{
                border-bottom: 1px solid grey;

                .searchForm{
                    padding: 10px;

                    input{
                        background-color: transparent;
                        border: none;
                        width: 100%;
                        font-size: 12px;
                        color: white;
                        outline: none;

                        &::placeholder{
                            color: lightgray;
                        }
                    }
                }
            }

            .userChat{
                padding: 10px;
                display: flex;
                align-items: center;
                gap: 10px;
                color: white;
                cursor: pointer;

                &:hover{
                    background-color: #2f2d52;
                }

                img{
                    width: 50px;
                    height: 50px;
                    border-radius: 50%;
                    object-fit: cover;
                    @include mobile(){
                        display: none;
                    }
                }

                .userChatInfo{
                    span{
                        font-size: 18px;
                        font-weight: bold;
                    }

                    p{
                        font-size: 14px;
                        color: lightgray;
                    }
                }
            }
        }

        .chat{
            flex: 2;

            .chatInfo{
                height: 50px;
                background-color: #5d5b8d;
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 10px;

                span{
                    font-size: 18px;
                    font-weight: bold;
                    color: #ddddf7;
                }

                img{
                    height: 35px;
                    width: 35px;
                    cursor: pointer;
                    padding: 0px 2px;
                }
            }

            .messages{
                background-color: #ddddf7;
                padding: 10px;
                height: calc(100% - 160px);
                overflow: scroll;

                .message{
                    display: flex;
                    gap: 20px;
                    margin-bottom: 10px;

                    .messageInfo{
                        display: flex;
                        flex-direction: column;
                        color: grey;

                        img{
                            width: 40px;
                            height: 40px;
                            border-radius: 50%;
                            object-fit: cover;
                        }
                    }

                    .messageContainer{
                        max-width: 80%;
                        display: flex;
                        flex-direction: column;
                        gap: 10px;

                        p{
                            background-color: white;
                            padding: 10px 20px;
                            border-radius: 0px 15px 15px 15px;
                        }
                    }

                    &.owner{
                        flex-direction: row-reverse;

                        .messageContainer{
                            align-items: flex-end;
                            p{
                                border-radius: 15px 15px 0px 15px;
                                background-color: #8da4f1;
                                color: white;
                            }
                        }
                    }
                }
            }

            .input{
                height: 50px;
                background-color: white;
                padding: 10px;
                display: flex;
                justify-content: space-between;

                input{
                    color: #2f2d52;
                    border:none;
                    outline: none;
                    font-size: 18px;
                    width: 100%;

                    &::placeholder{
                        color: lightgray;
                    }
                }

                .send{
                    display: flex;
                    align-items: center;
                    gap: 10px;

                    img{
                        height: 30px;
                        width: 30px;
                        cursor: pointer;
                    }

                    button{
                        background-color: #8da4f1;
                        color: white;
                        cursor: pointer;
                        border: none;
                        // height: 35px;
                        padding: 10px 15px;
                    }
                }
            }
        }
    }
}